import React from "react";
import swal from 'sweetalert';
import { Container, 
   Card, CardBody, Form,CardHeader,
 FormInput,
  FormGroup,Button } from "shards-react";
import { appConfig } from '../utils';
import {userDetails} from '../utils';
const { useState } = React;

const Placeorder = (props) => {
    const [customerId, setCustomerId] = useState();
    const [productId, setProductId] = useState();
    const [quantity, setQuantity] = useState();
    const [price, setPrice] = useState();
  
  
  
    const handleSubmit = (event) => {
      event.preventDefault();
      const Url = appConfig.webApi + "/placeOrder/addPlaceOrders";
      fetch(Url, {
        method: 'post',
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          'Access-Control-Allow-Origin': '*'
        },
        body: JSON.stringify({
            
                "userId": userDetails().id,
                "customerId": customerId,
                "orders": [
                    {
                        "productId": productId,
                        "quantity": quantity,
                        "price": price
                    }
                ]
        })
      }).then(function (response) {
        return response.json();
      }).then(function (responseData) {
        if (responseData.status == "Order Placed Successfully") {
          swal(responseData.status);
          swal({
            title: responseData.status,
            text: "Find Your Order In Orders List",
            icon: "success",
           })
        } else{
            swal("Error Occured");
        }
      })
    }
  
   const handleChange=(e)=> {
     if(e.target.name == "customerId"){
        setCustomerId(e.target.value)
      }else if(e.target.name == "productId"){
        setProductId(e.target.value)
      }else if(e.target.name == "quantity"){
        setQuantity(e.target.value)
      }else if(e.target.name == "price"){
        setPrice(e.target.value)
      }
      // obj[e.target.name] = e.target.value;
      // this.setState(obj);
    }
    return (
        <>
         <Container fluid className="main-content-container px-4" style={{marginTop:"1%"}}>
        <Card small className="h-100">
        <CardHeader className="border-bottom">
          <h4 className="m-0">Place Order</h4>
        </CardHeader>
        <CardBody>
        <Form style={{ marginTop: "2%"}} onSubmit={handleSubmit}>
            <FormGroup>
            <FormInput required onChange={handleChange} name="customerId" placeholder="Customer Id" />
           </FormGroup>
          <FormGroup>
            <FormInput required onChange={handleChange} name="productId" placeholder="Product Id" />
          </FormGroup>
          <FormGroup>
          <FormInput required onChange={handleChange} name="quantity" placeholder="Quantity"/>
        </FormGroup>
        <FormGroup>
          <FormInput required onChange={handleChange} name="price" placeholder="Price" />
        </FormGroup>
        {/* <FormGroup>
        <FormInput required onChange={handleChange} value="Active" readOnly name="status" placeholder="Status" />
            </FormGroup > */}
     <Button theme="success" type="submit">
          Place Order
        </Button>
      </Form>
        </CardBody>
        </Card>
      </Container>
       
          </>
);
};    

export default Placeorder;